let {setDefData, getDefData } = cardCache;
let dataSource = "setpart-grp";

let config = {
	nodeName: '10141486-000033',
    nodetype: 'group',
    pageCode: '10141486_list',
    appid: '0001Z0100000000019BE',
    searchId: 'search',
    tableId: 'ic_setpart',
    oid: '1002Z81000000000HGVY',
    pk_item: 'pk_setpart',
    formId:'head',
    appcode:'10141486',
    template:'10141486_list'//
}

